import React, { Component } from 'react'
// Tours SCSS
import './Tours.scss'
// Font Awesome icons
import { FaWindowClose, FaCaretSquareDown } from "react-icons/fa";
export default class Tours extends Component {
    state = {
        showInfo: false
    }

    handleInfo = () => {
        this.setState({
            showInfo: !this.state.showInfo
        })
    }
    render() {
        // check data of Tours
        // console.log(this.props)
        // Destructuring the props
        const { id, city, img, name, info } = this.props.tour
        const { removeTour } = this.props
        return (
            <article className="tour">
                <div className="imgContainer">
                    <img src={img} alt="tour-img" />
                    <span className="closeBtn" onClick={() => removeTour(id)}>
                        <FaWindowClose></FaWindowClose>
                    </span>
                </div>

                <div className="tourInfo">
                    <h3>{city}</h3>
                    <h4>{name}</h4>
                    <h5>info <span onClick={this.handleInfo}><FaCaretSquareDown></FaCaretSquareDown></span></h5>
                    {
                        this.state.showInfo && <p>{info}</p>
                    }
                    {/* <p>{info}</p> */}
                </div>
            </article>
        )
    }
}
